import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  MessageBar,
  Space,
  List,
  ListItem,
  Form,
  FieldText,
  IconButton,
  Spinner,
} from '@looker/components'
import { Search as SearchIcon } from '@styled-icons/material'

export const Search = ({
  onSearch,
  onSelected,
  data = [],
  error,
  loading,
  embedRunning,
  searchCriteria,
}) => {
  const [criteria, setCriteria] = useState('')

  useEffect(() => {
    setCriteria(searchCriteria || '')
    if (
      searchCriteria &&
      searchCriteria.length > 0 &&
      searchCriteria !== criteria
    ) {
      onSearch(searchCriteria)
    }
  }, [searchCriteria])

  const onSubmit = (e) => {
    e.preventDefault()
    onSearch(criteria.trim())
  }

  return (
    <Box height="33%" width="100%" borderBottom="solid 1px" borderColor="ui2">
      <Form onSubmit={onSubmit} mb="none">
        <Space px="small">
          {error && <MessageBar intent="critical">{error}</MessageBar>}
          <FieldText
            placeholder="Enter search criteria"
            value={criteria}
            onChange={(e) => setCriteria(e.target.value)}
            disabled={loading}
          />
          {loading ? (
            <Spinner />
          ) : (
            <IconButton
              type="submit"
              size="large"
              label="Search"
              disabled={criteria.trim().length === 0}
              icon={<SearchIcon />}
            />
          )}
        </Space>
      </Form>
      <List mt="none" density={-2} height="85%">
        {data.map(({ description, id }) => (
          <ListItem
            key={id}
            onClick={() => onSelected(id)}
            disabled={embedRunning}
          >
            {description}
          </ListItem>
        ))}
      </List>
    </Box>
  )
}

Search.propTypes = {
  searchCriteria: PropTypes.string,
  onSearch: PropTypes.func.isRequired,
  onSelected: PropTypes.func.isRequired,
  data: PropTypes.array,
  error: PropTypes.string,
  loading: PropTypes.bool,
  embedRunning: PropTypes.bool,
}
